@import 'components/variables';

.content-intro {
  background: #0292eb;
  background-image: url(../images/index/welcome-index.png);
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  .container {
    position: relative;
    height: 100%;
    .logo {
      position: absolute;
      width: 25vh;
      top: 3rem;
      left: 50%;
      transform: translateX(-50%);
      @media (min-width: 768px) {
        left: 0;
        transform: translateX(0);
      }
    }
    .content-form {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 70%;
      transform: translate3d(-50%, -50%, 0);
      p {
        font-family: Montserrat;
        font-size: 4rem;
        font-weight: 800;
        color: $white;
        text-align: center;
        span {
          font-family: Montserrat;
          font-weight: 400;
        }
      }
      select {
        border: none;
        background-color: rgba($color: #ffffff, $alpha: 0.8);
        color: $gray-siroco;
        @media (min-width: 768px) {
          border-radius: 0;
          &.select1 {
            border-top-left-radius: 5px;
            border-bottom-left-radius: 5px;
            border-right: 1px solid lightgray;
          }
          &.select3 {
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            border-left: 1px solid lightgray;
          }
        }
        &:focus {
          box-shadow: none;
        }
      }
      a {
        letter-spacing: normal;
        line-height: 1.4;
      }
    }
  }
  .dropdown {
    position: absolute;
    bottom: 3rem;
  }
  .dropdown-toggle::after {
    border-top: .3em solid $white !important;
    position: relative;
    left: 30px;
  }
  .dropdown-menu {
    min-width: 9rem !important;
    padding: 0 !important;
    border-radius: 1.25rem !important;
    li {
      margin-bottom: 0 !important;
    }
  }
  .dropdown-item {
    color: $blue-claro;
    background-color: $white !important;
    &:hover {
      color: $white;
      background-color: $blue-claro !important;
    }
  }
  .btn-idiomas {
    border-radius: 1.2em;
    background-color: #3B90DB;
    text-align: left;
    max-width: 200px;
    color: $white;
    .arrow-down {
      width: 0.8rem;
    }
  }
  .whatsapp {
    position:fixed;
    width:60px;
    height:60px;
    bottom:40px;
    right:40px;
    background-image: url("../images/bg-whatsapp.png");
    background-size: cover;
    color:$white;
    border-radius:50px;
    text-align:center;
    font-size:30px;
    z-index:100;
  }
  .whatsapp-icon {
    width: 60%;
    margin-top: 10px;
  }
}